import React, { useEffect } from "react";
import contact from "./contact.css";
import Aos from "aos";
import "aos/dist/aos.css";





function Contact() {

    useEffect(() => {
        Aos.init({duration: 900});
    });
    

    return <div className="wrapper-contact">
        <div data-aos="zoom-out-down" className="form-wrapper">
            <h1 id="titlu-contact">Send me your opinion.</h1>
            <form>
                <div className="form-inner container ">
                    <label className="label " htmlFor="nume">Name:</label><br></br>
                    <input data-aos="fade-right" data-aos-duration="1500" type="text" className="input form-control" name="nume" placeholder="Jack Jones"></input>

                    <label className="label" htmlFor="email">Email:</label><br></br>
                    <input data-aos="fade-left" data-aos-duration="1500" type="text" className="input form-control" name="email" placeholder="jones.jack@gmail.com"></input>
                    
                    <label className="label mt-3" htmlFor="textarea ">Toughts:</label><br></br>
                    <textarea  data-aos="fade-down" data-aos-duration="1500" className="textarea form-control" name="textarea" placeholder="your toughts..."></textarea>
                    
                    <button type="submit" className="btn btn-success mt-3 mb-3 ">Send</button>
                </div>
            </form>
        </div>
        
    </div>
}

export default Contact;